import React, { Component } from 'react'
import styled from 'styled-components'
import AboutMeImg from '../../../images/aboutMe.png'
export default class AboutMe extends Component {
    render() {
        return (
            <Content >
                        <Title id='about'>Sobre mim</Title>
                        <Container>
                            <Image><img src={AboutMeImg} alt="aboutme" width={"300vh"}/></Image>
                            <Right>
                            <Subtitle>Sou Victor, tcnico em redes e programador</Subtitle>
                            <Text>Possuo formao de nvel tcnico em Redes de Computadores pelo Instituto Federal de Sergipe (2018-2021). Sou atuante na rea de desenvolvimento de aplicativos pelo grupo de pesquisa GRUFEE onde houve a criao do aplicativo L2 (2019), j atuei em projetos de extenso como PIBIC em desenvolvimento de sites e aplicaes (2019). Atualmente sou estudante de Engenharia de Computao na Universidade Federal de Sergipe(2021)</Text>
                        </Right>  
                </Container>
            </Content>
        )

    }
}
export const Content = styled.div`
    display:flex;
    flex-direction:column;
    margin-bottom:10vh;
`

export const Container = styled.div`
    display:flex;
    @media (max-width: 1020px) {
        flex-direction:column;
    }
    `
export const Title = styled.h2`
    text-align:center;
`
export const Subtitle= styled.h3`
    text-align:center;
`
export const Text = styled.p`
    text-align:center;
  
`
export const Right = styled.div``
export const Image = styled.div`
    border:2px solid #000;
    border-radius:10px;
    margin-right:3vh;
    padding:3px 6px;
    justify-self:center;

    @media (max-width: 1020px) {
        display:flex;
        align-self:center;
        margin:0;
    }
`
